import React from 'react'
import HeaderOfPage from './HeaderOfPage'


const IntroductionPage = () => {
  return (
    <section className='w-full flex flex-col items-center'>
          <HeaderOfPage title="giới thiệu" />
          <div className='width-screen text-justify leading-8'>
               <p 
                    className='my-10'
               >Big Green là công ty thương mại điện tử chuyên cung cấp các thực phẩm hữu cơ và tự nhiên
                    với chuỗi cửa hàng thực phẩm hữu cơ với mục tiêu giúp người tiêu dùng Việt Nam có một cuộc
                    sống khỏe mạnh hơn thông qua những loại thực phẩm hữu cơ có chứng nhận, thực phẩm tự nhiên
                    và không có nguồn gốc biến đổi gene (GMO). Chúng tôi lựa chọn các loại thực phẩm hữu cơ, thực
                    phẩm tự nhiên từ các nhà sản xuất, các công ty trong và ngoài nước thông qua quá trình lựa chọn
                    kỹ càng về khả năng cung ứng, các giấy chứng nhận tiêu chuẩn do các tổ chức uy tín thế giới cấp.
                    Chúng tôi yêu thích những gì chúng tôi làm và chúng tôi đam mê những lợi ích của một lối sống lành
                    mạnh, tìm nguồn cung cấp sản phẩm hữu cơ chất lượng cao nhất cho khách hàng và cung cấp dịch
                    vụ giao hàng tận nhà tốt nhất. Chúng tôi hoàn toàn tin tưởng rằng những sản phẩm tạo ra từ quá 
                    trình canh tác và sản xuất theo phương thức hữu cơ và tự nhiên tốt cho cơ thể mọi người, tốt hơn 
                    cho cộng đồng và tốt hơn cho hành tinh mà chúng ta đang sống. Một lần nữa, Big Green được sáng 
                    lập bởi các nhà sáng lập muốn tạo dựng một cộng đồng thực phẩm sạch, dựa trên nền tảng hữu cơ
                    (organic) và thuần từ thiên nhiên, nhằm mang lại sức khoẻ tốt nhất cho cộng đồng.
               </p>
               <p
                    className='mb-20'
               >
                    Sứ mệnh của Big Green đó là giúp mọi người dễ dàng tiếp cận hơn với các loại thực phẩm hữu cơ, 
                    thực phẩm tự nhiên. Không chỉ cung cấp các sản phẩm hữu cơ, chúng tôi còn đem đến những thông
                    tin hữu ích về sức khỏe mà thực phẩm hữu cơ đem lại cho con người và cộng đồng. Mỗi người có 
                    nhu cầu và cách tiếp cận với thực phẩm hữu cơ, thực phẩm tự nhiên theo một cách khác nhau, 
                    vì vậy, chúng tôi có mặt ở đây để hỗ trợ bạn bằng cách: Chỉ cung cấp những loại thực phẩm hữu cơ, 
                    thực phẩm tự nhiên đạt những chứng nhận uy tín nói chung và được kiểm chứng bởi Big Green nói 
                    riêng. Khởi tạo những mối quan hệ tích cực, lâu dài và tin tưởng giữa Big Green với khách hàng, 
                    nhân viên, nhà cung cấp và cộng đồng. Hỗ trợ phát triển các trang trại, cộng đồng nhỏ vùng sâu 
                    vùng xa, vùng dân tộc ít người và các đối tượng dễ bị tổn thương trong xã hội canh tác theo 
                    phương thức hữu cơ, phương thức tự nhiên để có cuộc sống tốt đẹp hơn.
               </p>
          </div>
    </section>
  )
}

export default IntroductionPage